@import "~react-image-gallery/styles/css/image-gallery.css";

body {
  background: #fff;
}

#root {
  max-width: 1366px;
  margin: 0 auto;
  padding: 15px;
  background-color: #fff;
}

h1 {
  border-bottom: 1px solid #ccc;
  padding: 0 0 15px;
}

#simple-select, .MuiMenuItem-root {
  text-transform: capitalize;
}

.MuiCardHeader-title {
  font-size: 14px !important;
  color: #000;
}

.MuiCardHeader-subheader {
  color: #000 !important;
  font-weight: bold !important;
  font-size: 22px !important;
  overflow: hidden;
  text-overflow: ellipsis;
}

.MuiOutlinedInput-notchedOutline {
  border-color: #4f008c !important;
}

.MuiMenu-paper {
  border-radius: 20px !important;
}

.MuiMenu-list {
  padding: 0 !important;
}

.Mui-focused .MuiOutlinedInput-notchedOutline {
  border-width: 1px !important;
}

.MuiSelect-select.MuiSelect-outlined {
  padding: 18px 14px !important;
}

.MuiButtonBase-root.MuiMenuItem-root {
  padding-top: 15px !important;
  padding-bottom: 15px !important;
}

.MuiMenuItem-root.Mui-selected {
  background-color: transparent !important;
}

.offer-section {
  position: absolute;
  width: calc(100% - 80px);
  margin: 0 auto;
  border-top-left-radius: 18px;
  border-top-right-radius: 18px;
  top: -40px;
  left: 10%;
  text-align: center;
  height: 40px;
  line-height: 40px;
  background: #FF375E !important;
  font-size: 16px;
  color: #fff;
  font-weight: bold;
}

.MuiCard-root {
  position: relative;
  overflow: initial !important;
}

.MuiCard-root:hover {
  transition: all .5s ease 0s;
  transform: translateY(-5px);
}

.MuiCard-root:hover .primary-button {
  background-color: #ff375e;
  color: #ffffff !important;
}

.MuiCardActionArea-root:hover .MuiCardActionArea-focusHighlight {
  opacity: 0 !important;
}

.MuiCardActionArea-root:hover {
  background-color: #fff !important;
}

.product-img {
  height: 275px;
  margin: 0 auto;
  display: flex;
}

.product-card-img {
  width: auto !important;
  height: 250px;
  max-width: 100%;
  margin: 0 auto;
}

.css-46bh2p-MuiCardContent-root {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.product-price {
  display: flex;
  justify-content: center;
  line-height: normal !important;
}

.product-price .price-label {
  font-weight: bold;
  font-size: 36px;
}

.product-price .price-label-month {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 16px;
  line-height: 16px;
}

.product-price sup {
  font-weight: bold;
}

.product-price sup, .product-price sub {
  font-size: 16px;
}

.css-bhp9pd-MuiPaper-root-MuiCard-root {
  border-radius: 20px !important;
  box-shadow: 0 8px 30px -8px rgba(29,37,45,.3) !important;
}

.primary-button {
  border-radius: 4px !important;
  border: 1px solid #ff375e !important;
  font-size: 15px !important;
  width: 100% !important;
  color: #ff375e !important;
  padding: 13px 35px !important;
  text-transform: none !important;
  font-weight: bold !important;
}

.primary-button:hover {
  border: 1px solid #ff375e !important;
  color: #ffffff !important;
  background-color: #ff375e !important;
}

.secondary-button {
  background-color: #ff375e !important;
  border-radius: 4px !important;
  font-size: 15px !important;
  color: #ffffff !important;
  padding: 14px 35px !important;
  min-width: 220px !important;
}

.MuiFormControlLabel-root {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.storage-radio .MuiButtonBase-root + span {
  border: 1px solid #5d696f;
  border-radius: 10px;
  box-shadow: 1px 2px 2px 0 rgba(214,222,226,.1), 0 2px 2px 0 rgba(214,222,226,.1);
  width: 77px;
  height: 35px;
  margin-right: 10px;
  text-align: center;
  line-height: 35px;
}

.storage-radio .MuiRadio-root.Mui-checked + span {
  border: 1px solid #ff375e;
  font-weight: bold;
}

.storage-radio .MuiRadio-root {
  display: none !important;
}

.color-radio .MuiButtonBase-root + span {
  height: 24px;
  width: 24px;
  min-width: 24px;
  display: block;
  margin: 0 15px 0 0;
  border-radius: 50%;
  background-clip: content-box;
  padding: 1px!important;
  cursor: pointer;
}

h3 {
  font-size: 19px !important;
  font-weight: bold !important;
}

.custom-radio .MuiRadio-root {
  display: none;
}

.custom-radio .MuiFormControlLabel-label {
  border: 1px solid #5d696f;
  padding: 10px;
  padding: 18px 25px;
  border-radius: 10px;
  width: 100%;
  margin-bottom: 12px;
  font-size: 12px;
}

.custom-radio.installment-radio .MuiFormControlLabel-label {
  padding: 13px 25px;
}

.custom-radio .MuiRadio-root.Mui-checked + span {
  border: 1px solid #ff375e;
}

.css-yw020d-MuiAccordionSummary-expandIconWrapper.Mui-expanded {
  transform: rotate(225deg) !important;
}

.css-10egq61 {
  position: relative;
}

.product-detail-container .MuiFormControl-root .MuiSvgIcon-root {
  position: absolute;
  right: 0;
  width: 14px !important;
  background: #ff375e;
  border-bottom-left-radius: 8px;
  border-top-right-radius: 8px;
  color: #fff;
  height: 14px !important;
}

.storage-radio .MuiSvgIcon-root {
  right: 11px !important;
}

.image-gallery-thumbnail {
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 1px 2px 2px 0 rgba(214,222,226,.1), 0 2px 2px 0 rgba(214,222,226,.1);
  border: solid 0.5px #5d696f;
  margin-bottom: 17px;
}

.image-gallery-thumbnail:hover, .image-gallery-thumbnail.active {
  border: 1px solid #ff375e;
}

.css-8je8zh-MuiTouchRipple-root {
  width: 24px;
  height: 24px;
  border: 1px solid transparent;
}

.Mui-checked .MuiTouchRipple-root {
  border: 1px solid #ff375e;
  margin: 0 auto 10px;
  border-radius: 50%;
  background-clip: content-box;
  padding: 1px;
}

.css-hyxlzm {
  border: 1px solid transparent !important;
}

.css-hyxlzm:hover {
  border: 1px solid #ff375e !important;
}

.Mui-checked .css-hyxlzm {
  border: 1px solid #ff375e !important;
}

.color-radio .MuiFormControlLabel-label {
  display: none !important;
}

.color-radio .MuiRadio-root {
  padding: 0;
}

.MuiFormLabel-root, .MuiFormLabel-root.Mui-focused {
  font-size: 12px !important;
  font-weight: bold !important;
  text-transform: uppercase;
  letter-spacing: 1px !important;
  color: #1d252d !important;
}

.plan-name {
  font-size: 24px !important;
  line-height: 24px !important;
  white-space: nowrap;
  max-width: 230px;
  text-overflow: ellipsis;
  overflow: hidden;
  font-weight: bold !important;
}

.plan-tag {
  position: relative;
  right: 0;
  top: 50%!important;
  transform: translateY(-50%)!important;
  border-radius: 17.5px;
  background-color: #ffdd40;
  font-size: 12px;
  padding: 5px 12px;
  margin-left: 10px;
}

.plan-info {
  font-size: 19px;
  line-height: 19px;
  font-weight: bold;
}

.your-selection .MuiFormControlLabel-root {
  margin-right: 0 !important;
  margin-bottom: 10px;
}

.color-radio .MuiFormControlLabel-root {
  margin-right: 10px !important;
}

.device-payment .MuiFormControlLabel-root, .choose-plan .MuiFormControlLabel-root {
  width: 100%;
}

.custom-radio.price-radio .MuiFormControlLabel-label {
  min-height: 70px;
  padding: 12px 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
}

.full-price-label strike {
  color: #ff375e;
  font-weight: normal;
}

.full-price-label span {
  font-weight: normal;
  font-size: 12px;
  text-transform: none;
}

.choose-plan .MuiFormGroup-root .MuiBox-root {
  width: 50%;
}

.custom-radio.plan-radio .MuiFormControlLabel-label {
  min-height: 50px;
  padding: 12px 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
}

.features-title {
  font-size: 30px;
  padding-bottom: 20px;
  border-bottom: 2px solid #dde0e2;
  margin-bottom: 20px !important;
}

.fetures-desc {
  font-size: 14px;
  line-height: 1.86;
  letter-spacing: .5px;
}

.installment-label {
  font-size: 19px;
  font-weight: bold;
  letter-spacing: 1.14px;
}

.installment-label-val {
  font-size: 11px;
  font-weight: normal;
  letter-spacing: 1.1px;
  color: #5d696f;
  text-transform: uppercase;
  font-weight: 700;
}

.installment-label sup {
  font-size: 12px;
  font-weight: normal;
}

@media only screen and (min-width: 768px) {
  .image-gallery {
    position: fixed !important;
  }

  .img-container .image-gallery {
    position: absolute !important;
    bottom: 0;
  }

  .custom-radio .MuiFormControlLabel-label {
    font-size: 16px;
  }
}